.navbar {
    background-color: var(--primary-color);
    position: sticky;
    top: 0;
    z-index: 1000;
  }
  
  .navbar ul {
    display: flex;
    justify-content: center;
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .navbar li {
    margin: 0 15px;
  }
  
  .navbar a {
    color: white;
    text-decoration: none;
    padding: 15px 10px;
    display: inline-block;
    transition: color 0.3s ease;
  }
  
  .navbar a:hover,
  .navbar a.active {
    color: var(--accent-color-1);
  }
  
  @media (max-width: 768px) {
    .navbar ul {
      flex-direction: column;
      align-items: center;
    }
  
    .navbar li {
      margin: 10px 0;
    }
  }
  
  