.skill-chart {
    background-color: white;
    border-radius: 10px;
    padding: 15px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .skill-name {
    font-weight: bold;
    margin-bottom: 5px;
    display: block;
  }
  
  .skill-bar {
    height: 10px;
    background-color: #e0e0e0;
    border-radius: 5px;
    overflow: hidden;
  }
  
  .skill-level {
    height: 100%;
    background-color: var(--accent-color-1);
    transition: width 1s ease-in-out;
  }
  
  .skill-percentage {
    display: block;
    text-align: right;
    font-size: 0.8rem;
    color: var(--secondary-color);
    margin-top: 5px;
  }
  
  