.about {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .skills-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    width: 100%;
    margin-top: 30px;
  }
  
  @media (max-width: 768px) {
    .skills-container {
      grid-template-columns: 1fr;
    }
  }
  
  