.home {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    position: relative;
    overflow: hidden;
  }
  
  .hero-banner {
    z-index: 1;
  }
  
  .hero-banner h1 {
    font-size: 3rem;
    margin-bottom: 10px;
  }
  
  .hero-banner h2 {
    font-size: 1.5rem;
    color: var(--secondary-color);
    margin-bottom: 20px;
  }
  
  .background-animation {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(45deg, var(--primary-color), var(--secondary-color));
    opacity: 0.1;
    z-index: 0;
  }
  
  @keyframes gradientAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  .background-animation {
    animation: gradientAnimation 15s ease infinite;
    background-size: 400% 400%;
  }
  
  @media (max-width: 768px) {
    .hero-banner h1 {
      font-size: 2rem;
    }
  
    .hero-banner h2 {
      font-size: 1.2rem;
    }
  }
  
  